import "./App.css";
import { Button, Navbar, Container, Nav } from "react-bootstrap";
import data from "./db/fruit";
import data2 from "./db/veggie";
import { useState } from "react";
import Products from "./components/Products";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
import Detail from "./components/Detail";
import Title from "./components/Title";
import Title2 from "./components/Title2";
import ComVeggie from "./components/ComVeggie";
import Footer from "./components/Footer";
import axios from "axios";
import Cart from "./components/Cart";
import Board from "./components/Board";

function App() {
  let [fruit, setFruit] = useState(data);
  let [veggie, setVeggie] = useState(data2);
  let [count, setCount] = useState(1);
  let [input, setInput] = useState("");

  // console.log(fruit[0].price);
  let navigate = useNavigate();



  const sortByName = () => {
    let sortedFruit = [...fruit].sort((a, b) => (a.title > b.title ? 1 : -1));
    setFruit(sortedFruit);
    console.log(sortedFruit);
  };

  const sortByPriceLowToHigh = () => {
    let sortedFruit = [...fruit].sort((a, b) => a.price - b.price);
    setFruit(sortedFruit);
    console.log(sortedFruit);
  };

  const sortByPriceHighToLow = () => {
    let sortedFruit = [...fruit].sort((a, b) => b.price - a.price);
    setFruit(sortedFruit);
    console.log(sortedFruit);
  };
  return (
    <div className="App">
      <>
       <Navbar bg="dark" variant="dark">
          <Container>
            <Navbar.Brand  onClick={() => {   navigate("/");}}>  </Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link onClick={() => { navigate("/");}}> </Nav.Link>
              <Nav.Link onClick={() => { navigate("/detail/1"); }} >  </Nav.Link>
              <Nav.Link onClick={() => { navigate("/cart"); }}> </Nav.Link>
              <Nav.Link onClick={() => { navigate("/about");}}> </Nav.Link>
              <Nav.Link onClick={() => { navigate("/board");}}> </Nav.Link>
            </Nav>
          </Container>
 </Navbar>

        {/* <Link to="/"></Link><br></br>
        <Link to="/detail"></Link>  */}

        <Routes>
          <Route
            path="/"
            element={
              <div>
                {/*<div className="slider"></div>*/}
                <div className="slider" style={{ backgroundImage: `url(${process.env.PUBLIC_URL + '/img/slider.jpg'})`, }}></div>
                <Title />
                {/* <div class="container">
                    <div class="row">
                          <div style={{ textAlign: "center" }}>
                                <Button variant="outline-primary" onClick={sortByName}>   </Button>{" "}
                                <Button variant="outline-secondary" onClick={sortByPriceLowToHigh}> </Button>{" "}
                                <Button variant="outline-success" onClick={sortByPriceHighToLow}> </Button>{" "}
                          </div>
                      </div>
                </div> */}

                <div className="container">
                  <div className="row">
                    <div className="col-md-6" style={{ textAlign: "left" }}>
                      {/*   */}
                      <input
                        placeholder=" "
                        //    input  
                        onChange={(e) => setInput(e.target.value)}
                        value={input}
                        style={{
                          padding: "10px",
                          marginLeft: "10px",
                          borderRadius: "4px",
                          border: "1px solid #ccc",
                          width: "250px",
                          marginRight: "10px"
                        }}
                      />

                    </div>
                    <div className="col-md-6" style={{ textAlign: "right" }}>
                      {/* select */}
                      <select
                        onChange={(e) => {
                          if (e.target.value === "low") sortByPriceLowToHigh();
                          if (e.target.value === "high") sortByPriceHighToLow();
                          if (e.target.value === "name") sortByName();
                        }}
                        style={{
                          padding: "10px",
                          marginLeft: "10px",
                          borderRadius: "4px",
                          border: "1px solid #ccc",
                          width: "150px"
                        }}>
                        <option value=""> </option>
                        <option value="low"> </option>
                        <option value="high"> </option>
                        <option value="name"></option>
                      </select>
                    </div>
                  </div>
                </div>


                {/* <div className="container"  style={{marginTop:'30px'}}>
                      <div className="row">
                            {
                                fruit.map((ele,i)=>{
                                  return(
                                      <Products fruit={fruit[i]}  i={i}  key={fruit[i].id} />
                                  )
                                })
                              }
                        </div>
                  </div> */}

                <div className="container" style={{ marginTop: "30px" }}>
                  <div className="row">
                    {/* 'input'      */}
                    {/* .filter(...)      
                      .map(...)        */}
                    {fruit
                      .filter((item) =>
                        item.title.toLowerCase().includes(input.toLowerCase()) //  
                      )
                      .map((ele, i) => (
                        <Products fruit={ele} key={ele.id} /> //   
                      ))}
                  </div>
                </div>


                <div class="container">
                  <div class="row">
                    <div style={{ textAlign: "center" }}>
                      <Title2 />
                      <Button
                        variant="outline-success"
                        count={count}
                        onClick={() => {
                          if (count == 1) {
                            axios
                              .get(
                                "https://dino-21.github.io/react_data/veggie2.json"
                              )
                              .then((result) => {
                                let copy10 = [...veggie, ...result.data];
                                setVeggie(copy10);
                                setCount(count + 1);
                              });
                          } else if (count == 2) {
                            axios
                              .get(
                                "https://dino-21.github.io/react_data/veggie3.json"
                              )
                              .then((result) => {
                                let copy11 = [...veggie, ...result.data];
                                setVeggie(copy11);
                                setCount(count + 1);
                              });
                          }

                          if (count === 3) {
                            alert("  .");
                          }
                        }}
                      >
                        {" "}
                        + 3   {" "}
                      </Button>{" "}
                    </div>
                  </div>
                </div>

                <div className="container" style={{ marginTop: "30px" }}>
                  <div className="row">
                    {veggie.map((ele, i) => {
                      return (
                        <ComVeggie veggie={veggie[i]} key={veggie[i].id} />
                      );
                    })}
                  </div>
                </div>
                <Footer />
              </div>
            }
          />
          <Route path="/detail/:paramId" element={<Detail fruit={fruit} />} />
          <Route path="/cart" element={<Cart />} />
          <Route path="/about" element={<About />}>
            <Route path="member" element={<Member />} />
            <Route path="location" element={<Location />} />
          </Route>
          <Route path="/board" element={<Board/>} />
        </Routes>
      </>
    </div>
  );
}
//app , export default App; 
function About() {
  return (
    <>
      <h4></h4>
      <Outlet></Outlet>
    </>
  );
}

function Member() {
  return (
    <>
      <h4>Member</h4>
    </>
  );
}

function Location() {
  return (
    <>
      <h4>Location</h4>
    </>
  );
}

export default App;